import { useState, useEffect, useRef } from 'react';
import { Box, Paper, TextField, IconButton, Typography, Avatar } from '@mui/material';
import { styled } from '@mui/material/styles';
import SendIcon from '@mui/icons-material/Send';
import PetsIcon from '@mui/icons-material/Pets';
import { motion } from 'framer-motion';

const ChatContainer = styled(Paper)(({ theme }) => ({
  height: '90vh',
  margin: '20px',
  padding: theme.spacing(2),
  display: 'flex',
  flexDirection: 'column',
  background: 'rgba(255, 255, 255, 0.9)',
  backdropFilter: 'blur(10px)',
  borderRadius: '20px',
}));

const MessageBubble = styled(motion.div)(({ theme, isAI }) => ({
  maxWidth: '70%',
  padding: theme.spacing(2),
  borderRadius: isAI ? '20px 20px 20px 5px' : '20px 20px 5px 20px',
  backgroundColor: isAI ? theme.palette.primary.light : theme.palette.secondary.light,
  color: '#fff',
  margin: '8px',
  alignSelf: isAI ? 'flex-start' : 'flex-end',
  boxShadow: '0 4px 6px rgba(0,0,0,0.1)',
}));

function Chat() {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
  const messagesEndRef = useRef(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(scrollToBottom, [messages]);

  const handleSend = async () => {
    if (!input.trim()) return;
    
    const newMessage = {
      content: input,
      isAI: false,
      timestamp: new Date()
    };
    
    setMessages(prev => [...prev, newMessage]);
    setInput('');
    
    // Aqu ir la lgica para enviar el mensaje al backend
  };

  return (
    <Box sx={{ 
      height: '100vh',
      background: 'linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%)',
      padding: 2 
    }}>
      <ChatContainer elevation={3}>
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
          <Avatar sx={{ bgcolor: 'primary.main', mr: 2 }}>
            <PetsIcon />
          </Avatar>
          <Typography variant="h6">Alejandra - Veterinaria Virtual</Typography>
        </Box>
        
        <Box sx={{ flexGrow: 1, overflow: 'auto', mb: 2 }}>
          {messages.map((msg, i) => (
            <MessageBubble
              key={i}
              isAI={msg.isAI}
              initial={{ scale: 0.8, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              transition={{ duration: 0.3 }}
            >
              <Typography>{msg.content}</Typography>
            </MessageBubble>
          ))}
          <div ref={messagesEndRef} />
        </Box>

        <Box sx={{ display: 'flex', gap: 1 }}>
          <TextField
            fullWidth
            variant="outlined"
            placeholder="Escribe tu mensaje aqu..."
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyPress={(e) => e.key === 'Enter' && handleSend()}
            sx={{ 
              '& .MuiOutlinedInput-root': {
                borderRadius: '30px',
                backgroundColor: 'white'
              }
            }}
          />
          <IconButton 
            onClick={handleSend}
            sx={{
              backgroundColor: 'primary.main',
              color: 'white',
              '&:hover': {
                backgroundColor: 'primary.dark'
              }
            }}
          >
            <SendIcon />
          </IconButton>
        </Box>
      </ChatContainer>
    </Box>
  );
}

export default Chat;
